import React, { useState } from "react";
import { Link } from "react-router-dom";

export default function Header(): React.ReactElement {
    const [isOpen, setIsOpen] = useState(false);

    return (
        <header>
            <div className={`background ${isOpen ? "open" : ""}`}></div>
            <div className="header">
                <Link to="/" className="to-main-logo">
                    <img src="/images/logo.png" />
                </Link>
                <nav className={`${isOpen ? "open" : ""}`} onMouseEnter={() => setIsOpen(true)} onMouseLeave={() => setIsOpen(false)}>
                    <div className="nav-element">
                        <Link to="/" className="text-nav"></Link>
                    </div>
                    <div className="nav-element">
                        <Link to="/dsr/guides" className="text-nav"> </Link>
                    </div>
                    <div className="nav-element">
                        <Link to="/dsr/digimons/digidex" className="text-nav"></Link>
                        <div className="nav-subs">
                            <Link to="/dsr/digimons/digidex" className="text-nav"> </Link>
                            <Link to="/dsr/digimons/evolutions" className="text-nav"></Link>
                        </div>
                    </div>
                    <div className="nav-element">
                        <Link to="/maps" className="text-nav"></Link>
                    </div>
                    <div className="nav-element">
                        <Link to="/dungeons/overflows" className="text-nav"></Link>
                        <div className="nav-subs">
                            <Link to="/dungeons/detectors" className="text-nav"></Link>
                            <Link to="/dungeons/overflows" className="text-nav"> </Link>
                        </div>
                    </div>
                    {/* <div className="nav-element">
                        <Link to="/raids" className="text-nav"></Link>
                    </div> */}
                    <div className="nav-element">
                        <Link to="/dsr/tools/adjustments" className="text-nav"></Link>
                        <div className="nav-subs">
                            <Link to="/dsr/tools/adjustments" className="text-nav"> </Link>
                            <Link to="/dsr/tools/babies" className="text-nav"> </Link>
                            <Link to="/dsr/tools/potentials" className="text-nav"> </Link>
                            <Link to="/dsr/tools/skills" className="text-nav"> </Link>
                        </div>
                    </div>
                    <div className="nav-element">
                        <Link to="/dsr/items/combinations" className="text-nav"></Link>
                    </div>
                    <div className="nav-element">
                        <Link to="https://www.digimonsuperrumble.com" target="_blank" className="text-nav"> </Link>
                        <div className="nav-subs">
                            <Link to="https://www.digimonsuperrumble.com" target="_blank" className="text-nav"> </Link>
                            <Link to="https://cafe.naver.com/movedsr" target="_blank" className="text-nav">  </Link>
                        </div>
                    </div>
                </nav>
            </div>
        </header>
        
    );
}