import React from "react";
import DigimonSkillTable from "./digimonSkillTable";
import { Skill } from "../../classes";
import { getNameExceptColon } from "../../functions/commons";

type DigimonSkillProps = { digimonName: string, skill: Skill }

export default function DigimonSkill({ digimonName, skill }: DigimonSkillProps): React.ReactElement {
    return (
        <div className="skill">
            <div className="skill-shortcut">
                <img src={`/images/${digimonName}_${getNameExceptColon(skill.name)}.png`} />
                <div className="skill-info">
                    <div className="skill-title">
                        <span className="skill-name">{skill.name}</span>
                        <img src={`/images/_${skill.element}.png`} title={`${skill.element} `} />
                    </div>
                    <div className="badges">
                        <span className={`badge ${skill.range === "" ? "melee" : "ranged"}`}>{skill.range}</span>
                        <span className={`badge ${skill.target === "" ? "enemy" : "team"}`}>{skill.target} {skill.targetCount}</span>
                        { skill.attackCount === 0 && <span className="badge team"></span> }
                        { skill.attackCount > 0 && <span className="badge ranged">{skill.attackCount}</span> }
                        { skill.additionalTurn && <span className="badge turn">{skill.additionalTurn}   </span> }
                        { skill.effect && <span className="badge effect">{skill.effect}</span> }
                        {/* <span className="badge range">{skill.range}</span> */}
                    </div>
                </div>
            </div>
            <DigimonSkillTable skill={skill} />
        </div>
    );
}