const getSortText = (text: string): string => {
    switch(text) {
        case "nmae":
            return "";
        case "hp":
            return "HP";
        case "sp":
            return "SP";
        case "str":
            return "";
        case "int":
            return "";
        case "spd":
            return "";
        case "def":
            return "";
        case "res":
            return "";
        case "1skill":
            return "1";
        case "2skill":
            return "2";
        case "3skill":
            return "3";
        default:
            return "";
    }
}

export default getSortText;