const getDigimonQualityText = (quality: string, isTag: boolean = true): string => {
    switch(quality) {
        case "":
            if(isTag) return "    <mark> 25%</mark>  .";
            else      return "     25%  .";
        case "":
            if(isTag) return "   <mark>  25% </mark>.";
            else      return "     25% .";
        case "":
            if(isTag) return "    <mark> 2</mark> .";
            else      return "     2 .";
        case "":
            if(isTag) return "   <mark>25%  </mark> .";
            else      return "   25%   .";
        case "":
            if(isTag) return "   <mark> </mark>.";
            else      return "    .";
        case "":
            if(isTag) return "     , <mark>   </mark>.";
            else      return "     ,    .";
        default:
            return "";
    }
}

export default getDigimonQualityText;