import React from "react";
import { Gauge } from "../../functions/adjustmentFunctions";

type StatusType = { gauges: Array<Gauge> }

export default function Status({ gauges }: StatusType): React.ReactElement {
    let hp = 0;
    let sp = 0;
    let str = 0;
    let int = 0;
    let def = 0;
    let res = 0;
    let spd = 0;

    gauges.forEach(gauge => {
        switch(gauge.type) {
            case " HP": {
                hp += gauge.value * 100;
                return;
            }
            case " SP": {
                sp += gauge.value * 100;
                return;
            }
            case "": {
                str += gauge.value * 100;
                return;
            }
            case "": {
                int += gauge.value * 100;
                return;
            }
            case "": {
                def += gauge.value * 100;
                return;
            }
            case "": {
                res += gauge.value * 100;
                return;
            }
            case "": {
                spd += gauge.value * 100;
                return;
            }
        }
    });

    return (
        <div className="stat-container">
            <div className="stat">
                <span className="title"> HP :</span>
                <span className={`value ${hp > 0 ? "active" : ""}`}>+ {hp}%</span>
            </div>
            <div className="stat">
                <span className="title"> SP :</span>
                <span className={`value ${sp > 0 ? "active" : ""}`}>+ {sp}%</span>
            </div>
            <div className="stat">
                <span className="title"> :</span>
                <span className={`value ${str > 0 ? "active" : ""}`}>+ {str}%</span>
            </div>
            <div className="stat">
                <span className="title"> :</span>
                <span className={`value ${int > 0 ? "active" : ""}`}>+ {int}%</span>
            </div>
            <div className="stat">
                <span className="title"> :</span>
                <span className={`value ${def > 0 ? "active" : ""}`}>+ {def}%</span>
            </div>
            <div className="stat">
                <span className="title"> :</span>
                <span className={`value ${res > 0 ? "active" : ""}`}>+ {res}%</span>
            </div>
            <div className="stat">
                <span className="title"> :</span>
                <span className={`value ${spd > 0 ? "active" : ""}`}>+ {spd}%</span>
            </div>
        </div>
    );
}